import React, { useContext } from "react";
import { Link, useNavigate } from "react-router-dom";
import logo from "../../Assets/images/freshcart-logo.svg";
import { UserContext } from "../../Context/UserContext";
import { cartContext } from "../../Context/CartContext";
import { useSelector } from "react-redux";


export default function Navbar() {
  // Redux
  let {counter} = useSelector((state)=>state.counter)
  // console.log(counter);
  //Context
  let {numOfCartItems} = useContext(cartContext)
  let {userToken ,setUserToken} = useContext(UserContext)
  //Routing
  let navigate = useNavigate()
  
  function logOut() {
    localStorage.removeItem("userToken")
    setUserToken(null) 
    navigate("/login")
  }


  
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-body-tertiary fixed-top">
        <div className="container-fluid">
          <Link className="navbar-brand" to="/">
            <img src={logo} alt="fresh market logo" />
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              {userToken !== null ?  <>    <li className="nav-item">
                <Link className="nav-link" to="/">
                  Home 
                </Link> 
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/products">
                  Products
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/categories">
                  Categories
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/brands">
                  Brands
                </Link>
              </li>
             </>:''}
             
          
            </ul>

            <ul className="d-flex align-items-center navbar-nav ms-auto mb-2 mb-lg-0">
            <li>
                <i className="fab fa-facebook mx-2"></i>
                <i className="fab fa-instagram mx-2"></i>
                <i className="fab fa-twitter mx-2"></i>
                <i className="fab fa-youtube mx-2"></i>
              </li>
              {userToken !== null ?<>
              <li className="nav-item position-relative">
                <Link className="nav-link" to="/cart">
                  <i className="fas fa-shopping-cart fa-lg"></i>
                  <span className="badge bg-main text-white p-1 ms-1  position-absolute top-0 end-0">
                      {counter}
                  </span>
                </Link>
              </li>
              
              <li className="nav-item">
                <span onClick={()=>logOut()} className="nav-link cursor-pointer">Log out</span>
              </li></>:<> <li className="nav-item"> 
                <Link className="nav-link" to="/login">
                  Login
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/register">
                  Register
                </Link>
              </li></>}
             
             
              
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}
